import React, { Component } from 'react';
import { string, arrayOf, shape, func } from 'prop-types';
import Helmet from 'react-helmet';
import HeroPreview from '../Post/Preview/Hero';
import MediumPostList from '../PostList/Medium';
import SmallPostList from '../PostList/Small';
import AdRect from '../Ad/AdRect';
import { post, postList } from '../Post/type';

export default class PostList extends Component {
  static propTypes = {
    hero: post,
    mediumList: postList,
    smallList: postList.isRequired,
    heroComponent: func,
    mediumListComponent: func,
    smallListComponent: func,
  };

  static defaultProps = {
    hero: null,
    mediumList: null,
    heroComponent: HeroPreview,
    mediumListComponent: MediumPostList,
    smallListComponent: SmallPostList,
  };

  render() {
    const { hero, mediumList, smallList,
            heroComponent: Hero,
            mediumListComponent: MediumList,
            smallListComponent: SmallList
    } = this.props;

    return (
      <div className="post-list">
        { !!hero && <Hero post={hero} /> }
        <AdRect slot="3696929149" />
        { !!mediumList && <MediumList posts={mediumList}/> }
        <SmallList posts={smallList}/>
      </div>
    );
  }
}



