import React, { Component } from 'react';
import PictureHero from '../Picture/Hero';
import DetailsInfo from '../Details/Info';
import DetailsComments from '../Details/Comments';
import PostLink from '../PostLink';
import { post } from '../type';

export default class HeroPreview extends Component {

  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post } = this.props;

    return (
      <section className="hero-post-preview">
        <div className="hero-post-preview__wrapper">
          <PictureHero post={post} shaded />
          <div className="hero-post-preview__details">
            <div className="hero-post-preview__details-margin">
              <DetailsInfo post={post} />
              <PostLink post={post}>
                <h2>{post.title}</h2>
                <div className="hero-post-preview__excerpt" dangerouslySetInnerHTML={{ __html: post.excerpt }} />
              </PostLink>
              <DetailsComments post={post} />
            </div>
          </div>
        </div>
      </section>
    );
  }
}



