import React, { Component } from 'react';
import { getContrastColor } from '../../utils/color';
import HeroSplash from './Preview/HeroSplash';

export default class Category extends Component {
  render() {
    const { post } = this.props;

    const backgroundColor = post.color;
    const color = getContrastColor(backgroundColor, '#eee', '#333');

    return (
      <div className="post-category">
        <HeroSplash post={post} className="post-category__content" color={color} backgroundColor={backgroundColor} >
          <h2 style={{ color }}>{post.title}</h2>
          <div style={{ color }} dangerouslySetInnerHTML={{ __html: post.html }} />
        </HeroSplash>
      </div>
    );
  }
}



