import React, { Component } from 'react';
import { object } from 'prop-types';
import Page from '../components/Page';
import Category from '../components/Post/Category';
import List from '../components/Post/List';
import Pagination from '../components/Navigation/Pagination';

export default class BlogCategoryList extends Component {

  static propTypes = {
    route: object,
  };

  getUrl = page => {
    const { header } = this.props.pathContext;
    const { category } = header;
    return page === 1 ? `/${category}/` :  `/${category}/${page}/`;
  };

  render() {
    const { header, latest, posts, page, total } = this.props.pathContext;

    return (
      <Page
        title={`Категория "${header.category}"`}
        description={header.description}
        path={this.getUrl(page)}
        posts={posts}
      >
        <List
          heroComponent={Category}
          hero={header}
          mediumList={latest.slice(0,3)}
          smallList={posts}
        />
        <Pagination current={page} total={total} getUrl={this.getUrl}/>
      </Page>
    );
  }
};



