import React, { Component } from 'react';
import SmallPreview from '../Post/Preview/Small';
import { postList } from '../Post/type';

export default class SmallPostList extends Component {
  static propTypes = {
    posts: postList.isRequired,
  };

  render() {
    const { posts } = this.props;
    return (
      <div className="small-post-list">
        {posts.map(post => <SmallPreview key={post.id} post={post} />)}
      </div>
    );
  }
}



