import React, { Component } from 'react';
import Helmet from 'react-helmet';
import Link from 'gatsby-link';
import classnames from 'classnames';
import { getCanonical } from '../../utils/url';


export default class Pagination extends Component {
  render() {
    const { current, total, getUrl, showPages } = this.props;

    const pages = [...new Array(total).keys()];

    const curUrl = getUrl(current);
    const prevUrl = getUrl(Math.max(1, current - 1));
    const nextUrl = getUrl(Math.min(total, current + 1));

    return (
      <div className="pagination">
        <Helmet>
          {
            prevUrl !== curUrl &&
            <link rel="prev" href={getCanonical(prevUrl)} />
          }
          {
            nextUrl !== curUrl &&
            <link rel="next" href={getCanonical(nextUrl)} />
          }
        </Helmet>
        {
          total > 1 &&
          (<div className="pagination__direction">
            <Link to={prevUrl} className={classnames('link', { 'link--disabled': current === 1 }, 'pagination__direction-button')}>
              <i className="material-icons pagination__direction-icon">chevron_left</i> Сюда
            </Link>
            <Link to={nextUrl} className={classnames('link', { 'link--disabled': current === total }, 'pagination__direction-button')}>
              Туда <i className="material-icons pagination__direction-icon">chevron_right</i>
            </Link>
          </div>)
        }
        {
          total > 1 &&
          showPages &&
          <div className="pagination__posts">
            {pages.map(page =>
              (<div key={`page_${page}`}>
                <Link className={classnames('link', { 'link--disabled': current === page + 1 }, 'pagination__page')} to={getUrl(page + 1)}>{page + 1}</Link>
              </div>),
            )}
          </div>
        }
      </div>
    );
  }
}



