import React, { Component } from 'react';
import Figure from './Figure';
import { post } from '../type';

export default class Small extends Component {
  static propTypes = {
    post: post.isRequired,
  };
  render() {
    const { post } = this.props;
    return (
      <Figure className="small-post-preview__figure">
        <source srcSet={`${post.images.smallPreview.src} 120w`} />
        <img src={post.images.smallPreview.src} alt={post.title} className="small-post-preview__image"  style={{ backgroundImage: `url(${post.images.blurUp.src})`, backgroundSize: 'cover' }} />
      </Figure>
    );
  }
}



