import React, { Component } from 'react';
import { arrayOf, shape } from 'prop-types';
import PictureSmall from '../Picture/Small';
import DetailsInfo from '../Details/Info';
import DetailsComments from '../Details/Comments';
import PostLink from '../PostLink';
import { post } from '../type';

export default class SmallPreview extends Component {

  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post } = this.props;

    return (
      <div className="small-post-preview">
        <div className="small-post-preview__wrapper">
          <PostLink post={post}>
            <PictureSmall post={post} />
          </PostLink>
          <div className="small-post-preview__details">
            <DetailsInfo post={post} dark={true} />
            <PostLink post={post}>
              <h3 className="small-post-preview__title">{post.title}</h3>
              <div className="small-post-preview__excerpt" dangerouslySetInnerHTML={{ __html: post.excerpt }} />
            </PostLink>
            <DetailsComments post={post} />
          </div>
        </div>
      </div>
    );
  }
}



